/* Import romantic fonts */
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&family=Poppins:wght@300;400;500;600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.app {
  position: relative;
  min-height: 100vh;
}

/* Animated gradient background */
.gradient-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(-45deg, #ff9a9e, #fecfef, #fecfef, #ff9a9e, #a8edea, #fed6e3);
  background-size: 400% 400%;
  animation: gradientShift 15s ease infinite;
  z-index: -1;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Floating elements */
.floating-element {
  position: fixed;
  font-size: 2rem;
  pointer-events: none;
  animation: floatUp 5s linear forwards;
  z-index: 1;
}

@keyframes floatUp {
  from {
    opacity: 0;
    transform: translateY(100vh) rotate(0deg);
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translateY(-100px) rotate(360deg);
  }
}

/* Hero Section */
.hero-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  position: relative;
  padding: 1.5rem;
  background-image: url('./background.png');
  background-size: contain; /* Changed from cover to contain for better fitting */
  background-position: center;
  background-repeat: no-repeat;
}

.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.20); /* 75% transparency overlay */
  z-index: 1;
}

.hero-section > * {
  position: relative;
  z-index: 2; /* Ensure all content appears above the overlay */
}

/* Responsive background for different screen sizes */
@media (max-width: 768px) {
  .hero-section {
    background-size: cover; /* Use cover on mobile for full screen */
    background-position: center;
  }
}

@media (min-width: 769px) {
  .hero-section {
    background-size: contain; /* Use contain on desktop to center and fit */
    background-position: center;
  }
}

.sparkles-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.sparkle {
  position: absolute;
  font-size: 2rem;
  animation: sparkleFloat 3s ease-in-out infinite;
}

.sparkle-1 {
  top: 20%;
  left: 10%;
  animation-delay: 0s;
}

.sparkle-2 {
  top: 30%;
  right: 15%;
  animation-delay: 1s;
}

.sparkle-3 {
  bottom: 30%;
  left: 20%;
  animation-delay: 2s;
}

.sparkle-4 {
  bottom: 20%;
  right: 10%;
  animation-delay: 1.5s;
}

@keyframes sparkleFloat {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
    opacity: 0.7;
  }
  50% {
    transform: translateY(-20px) rotate(180deg);
    opacity: 1;
  }
}

.main-title {
  font-family: 'Dancing Script', cursive;
  font-size: 4rem;
  font-weight: 700;
  color: #fff;
  text-shadow: 0 0 20px rgba(255, 182, 193, 0.8);
  margin-bottom: 2rem;
  animation: titleGlow 2s ease-in-out infinite alternate;
}

@keyframes titleGlow {
  from {
    text-shadow: 0 0 20px rgba(255, 182, 193, 0.8);
  }
  to {
    text-shadow: 0 0 30px rgba(255, 182, 193, 1), 0 0 40px rgba(255, 105, 180, 0.6);
  }
}

.birthday-cake {
  position: relative;
  margin: 2rem 0;
}

.cake-emoji {
  font-size: 8rem;
  animation: cakeBounce 2s ease-in-out infinite;
}

@keyframes cakeBounce {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

.candles {
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 20px;
}

.candle-flame {
  font-size: 1.5rem;
  animation: flicker 1s ease-in-out infinite alternate;
}

.flame-1 { animation-delay: 0s; }
.flame-2 { animation-delay: 0.3s; }
.flame-3 { animation-delay: 0.6s; }

@keyframes flicker {
  0% { transform: scale(1) rotate(-2deg); opacity: 0.9; }
  100% { transform: scale(1.1) rotate(2deg); opacity: 1; }
}

.confetti-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.confetti {
  position: absolute;
  font-size: 2rem;
  animation: confettiFall 3s linear infinite;
}

@keyframes confettiFall {
  from {
    transform: translateY(-100px) rotate(0deg);
    opacity: 1;
  }
  to {
    transform: translateY(100vh) rotate(360deg);
    opacity: 0;
  }
}

/* Generate random positions for confetti */
.confetti-0 { left: 5%; animation-delay: 0s; }
.confetti-1 { left: 15%; animation-delay: 0.2s; }
.confetti-2 { left: 25%; animation-delay: 0.4s; }
.confetti-3 { left: 35%; animation-delay: 0.6s; }
.confetti-4 { left: 45%; animation-delay: 0.8s; }
.confetti-5 { left: 55%; animation-delay: 1s; }
.confetti-6 { left: 65%; animation-delay: 1.2s; }
.confetti-7 { left: 75%; animation-delay: 1.4s; }
.confetti-8 { left: 85%; animation-delay: 1.6s; }
.confetti-9 { left: 95%; animation-delay: 1.8s; }
.confetti-10 { left: 10%; animation-delay: 2s; }
.confetti-11 { left: 30%; animation-delay: 2.2s; }
.confetti-12 { left: 50%; animation-delay: 2.4s; }
.confetti-13 { left: 70%; animation-delay: 2.6s; }
.confetti-14 { left: 90%; animation-delay: 2.8s; }

/* Card Section */
.card-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 4rem 2rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.card-section h2 {
  font-family: 'Dancing Script', cursive;
  font-size: 3rem;
  color: #fff;
  text-shadow: 0 0 15px rgba(255, 182, 193, 0.8);
  margin-bottom: 3rem;
}

.envelope {
  position: relative;
  width: 400px;
  height: 250px;
  cursor: pointer;
  transition: transform 0.3s ease;
  perspective: 1000px;
}

.envelope:hover {
  transform: scale(1.05);
}

.envelope-front {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #ff9a9e, #fecfef);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: transform 0.6s ease;
  backface-visibility: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.envelope-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.envelope-front p {
  color: #fff;
  font-weight: 500;
  text-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}

.envelope.open .envelope-front {
  transform: rotateY(-180deg);
}

.birthday-card {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #ffecd2, #fcb69f);
  border-radius: 15px;
  padding: 2rem;
  transform: rotateY(180deg);
  backface-visibility: hidden;
  transition: transform 0.6s ease;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  overflow-y: auto;
}

.envelope.open .birthday-card {
  transform: rotateY(0deg);
}

.card-header h3 {
  font-family: 'Dancing Script', cursive;
  font-size: 2rem;
  color: #d63384;
  text-align: center;
  margin-bottom: 1rem;
}

.card-content p {
  font-size: 1rem;
  line-height: 1.6;
  color: #5a5a5a;
  text-align: center;
  margin-bottom: 1.5rem;
}

.card-signature {
  font-family: 'Dancing Script', cursive;
  font-size: 1.2rem;
  color: #d63384;
  text-align: right;
  font-style: italic;
}

/* Memories Section */
.memories-section {
  padding: 4rem 2rem;
  text-align: center;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
}

.memories-section h2 {
  font-family: 'Dancing Script', cursive;
  font-size: 3rem;
  color: #fff;
  text-shadow: 0 0 15px rgba(255, 182, 193, 0.8);
  margin-bottom: 3rem;
}

.photo-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.photo-frame {
  position: relative;
  perspective: 1000px;
}

.photo {
  width: 100%;
  height: 200px;
  background: linear-gradient(135deg, #a8edea, #fed6e3);
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease;
  cursor: pointer;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  transform: rotate(-2deg);
}

.photo:hover {
  transform: rotate(0deg) scale(1.05);
}

.photo-placeholder {
  font-size: 3rem;
  opacity: 0.7;
}

.photo-caption {
  position: absolute;
  bottom: -40px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.9);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  color: #5a5a5a;
  font-weight: 500;
  white-space: nowrap;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.photo-frame:hover .photo-caption {
  opacity: 1;
}

/* Surprise Section */
.surprise-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 4rem 2rem;
  position: relative;
}

.surprise-btn {
  background: linear-gradient(135deg, #ff9a9e, #fecfef);
  border: none;
  border-radius: 50px;
  padding: 1.5rem 3rem;
  font-size: 1.2rem;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  gap: 1rem;
}

.surprise-btn:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
}

.gift-box {
  font-size: 2rem;
  animation: giftShake 2s ease-in-out infinite;
}

@keyframes giftShake {
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(-5deg); }
  75% { transform: rotate(5deg); }
}

.surprise-btn.opened .gift-box {
  animation: giftPop 0.6s ease-out;
}

@keyframes giftPop {
  0% { transform: scale(1); }
  50% { transform: scale(1.5) rotate(180deg); }
  100% { transform: scale(1) rotate(360deg); }
}

.surprise-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  animation: surpriseAppear 0.6s ease-out;
}

.surprise-gif-container {
  margin-bottom: 2rem;
  background: transparent;
}

.surprise-gif {
  max-width: 300px;
  max-height: 300px;
  border-radius: 20px;
  background: transparent;
  animation: gifAppear 0.8s ease-out;
  /* Remove box-shadow to make it cleaner */
}

@keyframes gifAppear {
  from {
    opacity: 0;
    transform: scale(0.5) rotate(-10deg);
  }
  to {
    opacity: 1;
    transform: scale(1) rotate(0deg);
  }
}

@keyframes surpriseAppear {
  from {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.5);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
}

.surprise-explosion {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.explosion-particle {
  position: absolute;
  font-size: 2rem;
  animation: explode 2s ease-out forwards;
}

@keyframes explode {
  from {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
  to {
    opacity: 0;
    transform: translate(var(--x, 0), var(--y, 0)) scale(0.5);
  }
}

.particle-0 { --x: -100px; --y: -100px; }
.particle-1 { --x: 100px; --y: -100px; }
.particle-2 { --x: -100px; --y: 100px; }
.particle-3 { --x: 100px; --y: 100px; }
.particle-4 { --x: -150px; --y: 0px; }
.particle-5 { --x: 150px; --y: 0px; }
.particle-6 { --x: 0px; --y: -150px; }
.particle-7 { --x: 0px; --y: 150px; }
.particle-8 { --x: -75px; --y: -75px; }
.particle-9 { --x: 75px; --y: -75px; }
.particle-10 { --x: -75px; --y: 75px; }
.particle-11 { --x: 75px; --y: 75px; }
.particle-12 { --x: -125px; --y: -50px; }
.particle-13 { --x: 125px; --y: -50px; }
.particle-14 { --x: -50px; --y: -125px; }
.particle-15 { --x: 50px; --y: -125px; }
.particle-16 { --x: -125px; --y: 50px; }
.particle-17 { --x: 125px; --y: 50px; }
.particle-18 { --x: -50px; --y: 125px; }
.particle-19 { --x: 50px; --y: 125px; }

.surprise-message {
  background: rgba(255, 255, 255, 0.95);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  margin-top: 100px;
}

.surprise-message h3 {
  font-family: 'Dancing Script', cursive;
  font-size: 2rem;
  color: #d63384;
  margin-bottom: 1rem;
}

.surprise-message p {
  color: #5a5a5a;
  font-size: 1.1rem;
}

/* Wishes Section */
.wishes-section {
  padding: 4rem 2rem;
  text-align: center;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Prevent lanterns from appearing outside the section */
}

.wishes-section h2 {
  font-family: 'Dancing Script', cursive;
  font-size: 3rem;
  color: #fff;
  text-shadow: 0 0 15px rgba(255, 182, 193, 0.8);
  margin-bottom: 1rem;
}

.wishes-section p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.wish-btn {
  background: linear-gradient(135deg, #667eea, #764ba2);
  border: none;
  border-radius: 50px;
  padding: 1rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.wish-btn:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
}

@keyframes lanternFloat {
  from {
    bottom: 10%;
    opacity: 1;
    transform: translateX(0) rotate(0deg);
  }
  to {
    bottom: 85%; /* Float to near the top and stay there */
    opacity: 0; /* Keep visible instead of fading out */
    transform: translateX(30px) rotate(0deg); /* Reduced movement */
  }
}

.floating-lantern {
  position: absolute;
  font-size: 3rem;
  animation: lanternFloat 20s ease-out forwards; /* Increased from 6s to 8s for even slower movement */
  z-index: 20;
  bottom: 10%;
}

.lantern-wish {
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.9);
  padding: 0.5rem 1rem;
  border-radius: 15px;
  font-size: 0.8rem;
  color: #5a5a5a;
  font-weight: 600;
  white-space: nowrap;
}

.fireflies {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.firefly {
  position: absolute;
  font-size: 1.5rem;
  animation: fireflyFloat 4s ease-in-out infinite;
}

@keyframes fireflyFloat {
  0%, 100% {
    transform: translateY(0px) translateX(0px);
    opacity: 0.7;
  }
  25% {
    transform: translateY(-20px) translateX(10px);
    opacity: 1;
  }
  50% {
    transform: translateY(-10px) translateX(-15px);
    opacity: 0.8;
  }
  75% {
    transform: translateY(-30px) translateX(5px);
    opacity: 1;
  }
}

.firefly-0 { top: 20%; left: 10%; animation-delay: 0s; }
.firefly-1 { top: 30%; left: 80%; animation-delay: 0.5s; }
.firefly-2 { top: 60%; left: 20%; animation-delay: 1s; }
.firefly-3 { top: 40%; left: 70%; animation-delay: 1.5s; }
.firefly-4 { top: 80%; left: 60%; animation-delay: 2s; }
.firefly-5 { top: 15%; left: 50%; animation-delay: 2.5s; }
.firefly-6 { top: 70%; left: 30%; animation-delay: 3s; }
.firefly-7 { top: 50%; left: 90%; animation-delay: 3.5s; }

/* Ending Section */
.ending-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 4rem 2rem;
  background: linear-gradient(135deg, rgba(255, 154, 158, 0.2), rgba(254, 207, 239, 0.2));
  backdrop-filter: blur(10px);
  position: relative;
  text-align: center;
}

.final-message h2 {
  font-family: 'Dancing Script', cursive;
  font-size: 3.5rem;
  color: #fff;
  text-shadow: 0 0 20px rgba(255, 182, 193, 0.8);
  margin-bottom: 1rem;
  animation: finalGlow 3s ease-in-out infinite alternate;
}

@keyframes finalGlow {
  from {
    text-shadow: 0 0 20px rgba(255, 182, 193, 0.8);
  }
  to {
    text-shadow: 0 0 30px rgba(255, 182, 193, 1), 0 0 40px rgba(255, 105, 180, 0.8);
  }
}

.final-message p {
  font-size: 1.3rem;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 3rem;
  font-weight: 500;
}

.floating-hearts {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
}

.floating-heart {
  position: absolute;
  font-size: 2rem;
  animation: heartFloat 6s linear infinite;
  opacity: 0.8;
}

@keyframes heartFloat {
  from {
    bottom: -50px;
    opacity: 1;
    transform: translateX(0) rotate(0deg);
  }
  to {
    bottom: 100vh;
    opacity: 0;
    transform: translateX(100px) rotate(360deg);
  }
}

.heart-0 { left: 5%; animation-delay: 0s; }
.heart-1 { left: 15%; animation-delay: 0.6s; }
.heart-2 { left: 25%; animation-delay: 1.2s; }
.heart-3 { left: 35%; animation-delay: 1.8s; }
.heart-4 { left: 45%; animation-delay: 2.4s; }
.heart-5 { left: 55%; animation-delay: 3s; }
.heart-6 { left: 65%; animation-delay: 3.6s; }
.heart-7 { left: 75%; animation-delay: 4.2s; }
.heart-8 { left: 85%; animation-delay: 4.8s; }
.heart-9 { left: 95%; animation-delay: 5.4s; }

.kiss-btn {
  background: linear-gradient(135deg, #ff6b9d, #c44569);
  border: none;
  border-radius: 50px;
  padding: 1.5rem 3rem;
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  margin-top: 2rem;
  position: relative;
  z-index: 10;
}

.kiss-btn:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
}

/* Kiss overlay */
.kiss-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 182, 193, 0.3);
  z-index: 1000;
  pointer-events: none;
}

.kiss-mark {
  position: absolute;
  font-size: 2rem;
  animation: kissAppear 3s ease-out forwards;
}

@keyframes kissAppear {
  0% {
    opacity: 0;
    transform: scale(0) rotate(0deg);
  }
  20% {
    opacity: 1;
    transform: scale(1.2) rotate(180deg);
  }
  80% {
    opacity: 1;
    transform: scale(1) rotate(350deg);
  }
  100% {
    opacity: 0;
    transform: scale(0.8) rotate(360deg);
  }
}

/* Permanent kisses overlay */
.permanent-kisses-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  pointer-events: none;
}

.permanent-kiss {
  position: absolute;
  color: #ff6b9d;
  text-shadow: 0 0 10px rgba(255, 107, 157, 0.7);
  animation: permanentKissGlow 2s ease-in-out infinite alternate;
}

@keyframes permanentKissGlow {
  from {
    text-shadow: 0 0 10px rgba(255, 107, 157, 0.7);
    filter: brightness(1);
  }
  to {
    text-shadow: 0 0 20px rgba(255, 107, 157, 1), 0 0 30px rgba(255, 105, 180, 0.8);
    filter: brightness(1.2);
  }
}

/* Big kiss overlay */
.big-kiss-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 182, 193, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1001;
  pointer-events: none;
  animation: bigKissAppear 1s ease-out;
}

.big-kiss {
  font-size: 15rem;
  color: #ff1744;
  text-shadow: 0 0 50px rgba(255, 23, 68, 0.8);
  animation: bigKissPulse 2s ease-in-out infinite;
}

@keyframes bigKissAppear {
  from {
    opacity: 0;
    transform: scale(0) rotate(-180deg);
  }
  to {
    opacity: 1;
    transform: scale(1) rotate(0deg);
  }
}

@keyframes bigKissPulse {
  0%, 100% {
    transform: scale(1);
    text-shadow: 0 0 50px rgba(255, 23, 68, 0.8);
  }
  50% {
    transform: scale(1.1);
    text-shadow: 0 0 80px rgba(255, 23, 68, 1), 0 0 100px rgba(255, 105, 180, 0.8);
  }
}

/* Music control */
.music-control {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea, #764ba2);
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  z-index: 100;
}

.music-control:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

/* Responsive Design */
@media (max-width: 768px) {
  .main-title {
    font-size: 2.5rem;
  }
  
  .cake-emoji {
    font-size: 5rem;
  }
  
  .envelope {
    width: 320px;
    height: 200px;
  }
  
  .card-section h2,
  .memories-section h2,
  .wishes-section h2 {
    font-size: 2.5rem;
  }
  
  .final-message h2 {
    font-size: 2.8rem;
  }
  
  .photo-gallery {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1.5rem;
  }
  
  .surprise-btn,
  .wish-btn,
  .kiss-btn {
    padding: 1rem 2rem;
    font-size: 1rem;
  }
  
  .surprise-gif {
    max-width: 250px;
    max-height: 250px;
  }
}

@media (max-width: 480px) {
  .main-title {
    font-size: 2rem;
  }
  
  .envelope {
    width: 280px;
    height: 180px;
  }
  
  .birthday-card {
    padding: 1.5rem;
  }
  
  .card-section h2,
  .memories-section h2,
  .wishes-section h2 {
    font-size: 2rem;
  }
  
  .final-message h2 {
    font-size: 2.2rem;
  }
  
  .surprise-gif {
    max-width: 200px;
    max-height: 200px;
  }
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #ff9a9e, #fecfef);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #ff6b9d, #c44569);
}